import React from "react";
import { MDBRow, MDBContainer, MDBCol } from "mdbreact";
import { Typography, Hidden } from "@material-ui/core";
import { Link } from "react-router-dom";
// import { ReactComponent as ErrorImg } from '../assets/icons/warning.svg';

export function PageNotFound() {
    return (
        <MDBContainer className="mt-10 mb-5 text-center">
            <MDBRow>
                <Hidden smDown>
                    <MDBCol md="6">
                        {/* <ErrorImg width="90%" /> */}
                    </MDBCol>
                </Hidden>
                <MDBCol className="d-flex justify-content-center flex-column align-items-center" md="12">
                    <Typography style={{ color: "orangered", fontSize: "10rem", fontWeight: 900, letterSpacing: 20 }}>404</Typography>
                    <h1>Uh oh, we couldn't find that page</h1>
                    <Link className="mt-5" to="/">Home</Link>
                </MDBCol>

            </MDBRow>
        </MDBContainer>
    );
}
