import React from 'react';
import { MDBView, MDBCarousel, MDBCarouselInner, MDBCarouselItem, MDBMask, MDBCarouselCaption } from 'mdbreact';
import { CSS } from '../assets/style';
import { Button } from '@material-ui/core';
import CustomBtn from './CustomBtn';


export default function TopCarousel() {
    const carouselList = [{
        title: 'do you want to feel better?',
        description: 'Get help now with a nutritional therapy session to boost energy, improve your mood and tackle any ill health issues.',
        url: 'https://image.shutterstock.com/image-photo/panoramic-banner-focus-fresh-vegetables-260nw-1471020857.jpg',
        btnLabel: "Book Appointment"
    },
    {
        title: 'easy book & pay online',
        description: 'Choose your time, pay & receive confirmation all in the comfort of your own home.',
        url: 'https://image.shutterstock.com/image-photo/diet-plant-based-protein-healthy-260nw-1691259316.jpg',
        btnLabel: "Book Now"
    },
    {
        title: 'do you want weight loss results that last?',
        description: 'Learn how to lose weight and keep it off!',
        url: 'https://www.docdonald.com/wp-content/uploads/2018/06/nutrition-banner.jpg',
        btnLabel: "Know More"
    }]
    return (
        <MDBCarousel
            activeItem={1}
            length={3}
            showControls={true}
            showIndicators={true}
            className="z-depth-1"
            style={{ ...CSS.carousel }}
        >
            <MDBCarouselInner>
                {carouselList.map((listItem, index) => <MDBCarouselItem key={index} itemId={++index}>
                    <MDBView>
                        <img
                            className="d-block w-100 carousel-height"
                            src={listItem.url}
                            alt={`${index} slide`}
                        />
                        <MDBMask overlay="black-light" />
                    </MDBView>
                    <MDBCarouselCaption className="m-3">
                        <h3 className="h3-responsive text-white">{listItem.title}</h3>
                        <p className="text-white">{listItem.description}</p>
                        <CustomBtn btnLabel={listItem.btnLabel} />
                    </MDBCarouselCaption>
                </MDBCarouselItem>)}
            </MDBCarouselInner>
        </MDBCarousel>
    );
}