import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Calendar from '../Calendar/Calendar';
import CustomBtn from '../CustomBtn';
import Time from '../Calendar/Time';
import ContactUsForm from '../Form/ContactUsForm';
import { connect } from 'react-redux';
import { updateSlots } from '../../Actions/ActionTypes';
import { Chip } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    button: {
        marginTop: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    resetContainer: {
        padding: theme.spacing(3),
    },
}));

function getChip(label: string) {
    return label || ''
}

function getSteps(slotDetails: any) {
    return [`Select Date ${getChip(slotDetails?.date)}`, `Slect Time ${getChip(slotDetails?.time)}`, 'Enter Details'];
}

function getStepContent(step: number, props: any) {
    switch (step) {
        case 0:
            return <Calendar slotDetails={props.slotDetails} />
        case 1:
            return <Time slotDetails={props.slotDetails}/>
        case 2:
            return null
        default:
            return 'Unknown step';
    }
}

function BookingStepper(props: any) {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps(props.slotDetails);

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        setActiveStep(0);
    };

    return (
        <div className={classes.root}>
            <Stepper activeStep={activeStep} orientation="vertical">
                {steps.map((label, index) => (
                    <Step key={label}>
                        <StepLabel>{label}</StepLabel>
                        <StepContent>
                            <Typography>{getStepContent(index, props)}</Typography>
                            <div className={classes.actionsContainer}>
                                <div>
                                    {activeStep ? <Button
                                        onClick={handleBack}
                                        className={classes.button}
                                    >Back</Button> : null}
                                    <CustomBtn
                                        handleButtonClick={handleNext}
                                        btnLabel={activeStep === steps.length - 1 ? 'Confirm' : 'Next'}
                                    />
                                </div>
                            </div>
                        </StepContent>
                    </Step>
                ))}
            </Stepper>
            {activeStep === steps.length && (
                <Paper square elevation={0} className={classes.resetContainer}>
                    <Typography>Booking Complete</Typography>
                    <CustomBtn
                        handleButtonClick={handleReset}
                        btnLabel="Book Again"
                    />
                </Paper>
            )}
        </div>
    );
}

const mapStateToProps = (state: any) => {
    return {
        slotDetails: state.SlotReducer.slotDetails
    }
}

export default connect(mapStateToProps, { updateSlots })(BookingStepper)
